// This file defines the strategy for parsing Go Modules' go.mod files and fetching license information.
//  strategy  parsing  go.mod  Go Modules  license

const { fetchJson } = require('../utils/network'); // Import the fetchJson function from the network utils.  fetchJson  network utils

const goModStrategy = {
    // The file name that this strategy handles.  strategy 
    fileName: 'go.mod',

    /**
     * Parses the go.mod file content and extracts the dependencies.
     * @param {string} fileContent The content of the go.mod file.
     * @returns {object} An object containing the dependencies.
     */
    // Parses the go.mod file content and extracts the dependencies.
    // Parse  go.mod  dependencies
    parseDependencies(fileContent) {
        // Initialize an empty object to store dependencies.  object  dependencies
        const deps = {};
        // Define a regex to find the 'require' block in the go.mod file.  regex  block 'require'  go.mod
        const requireRegex = /require\s*\(([^)]+)\)/;
        // Match the 'require' block in the file content.  'require' block 
        const requireMatch = fileContent.match(requireRegex);
        // If a 'require' block is found, use its content for parsing, otherwise use the whole file content.  'require' block,  parsing, 
        const contentToParse = requireMatch ? requireMatch[1] : fileContent;
        
        // Define a regex to find each dependency line in the content.  regex  dependency 
        const lineRegex = /^\s*([^\s]+)\s+([^\s]+)/gm;
        // Initialize a variable to store each match.  match
        let match;
        // Iterate over each match in the content.  match 
        while ((match = lineRegex.exec(contentToParse)) !== null) {
            // If the line is not a 'require' statement, a comment, or the 'go' version, add it to the dependencies object.  'require' statement, comment,  'go' version,  object dependencies
            if (match[1] !== 'require' && !match[1].startsWith('//') && match[1] !== 'go') {
                // Add the dependency name and version to the dependencies object.  dependency  version  object dependencies
                deps[match[1]] = match[2];
            }
        }
        // Return the dependencies object.  object dependencies
        return deps;
    },

    /**
     * Fetches the license information for a given package name and version from deps.dev.
     * @param {string} packageName The name of the package.
     * @param {string} packageVersion The version of the package.
     * @returns {Promise<object>} An object containing the license and homepage information.
     */
    // Fetches the license information for a given package name and version from deps.dev.
    //  license  package name  version  deps.dev
    async fetchLicenseInfo(packageName, packageVersion) {
        // Ensure the version starts with 'v' to be compatible with the deps.dev API.  version  'v'  deps.dev API
        const version = packageVersion.startsWith('v') ? packageVersion : `v${packageVersion}`;
        // Construct the API URL for fetching insights data from deps.dev.  API URL  insights  deps.dev
        const apiUrl = `https://deps.dev/_/s/go/p/${encodeURIComponent(packageName)}/v/${encodeURIComponent(version)}/insights`;

        try {
            // Fetch the insights data from the API.  insights  API
            const insightsData = await fetchJson(apiUrl);
            // Extract the licenses from the insights data.  licenses  insights
            const licenses = insightsData.licenses || [];
            
            // Return an object containing the license and homepage information.  object  license  homepage
            return {
                // Join the licenses with ' OR ' if there are multiple, otherwise return 'N/A'.  licenses  ' OR '  licenses,  'N/A'
                license: licenses.length > 0 ? licenses.join(' OR ') : 'N/A',
                // Provide a default homepage URL for Go packages.  URL homepage  Go packages
                homepage: `https://pkg.go.dev/${packageName}`
            };

        } catch (error) {
            // Log the error message if fetching insights data fails.  error  insights 
            console.error(`Failed to fetch Go insights for ${packageName} from deps.dev. Error: ${error.message}`);
            // Re-throw the error to be handled by the scanner. Re-throw error  scanner 
            throw error;
        }
    }
};

// Export the goModStrategy object.  object goModStrategy
module.exports = goModStrategy;