// This file defines the strategy for parsing Composer's composer.json files and fetching license information.
//  strategy  parsing  composer.json  Composer  license

const { fetchJson } = require('../utils/network'); // Import the fetchJson function from the network utils.  fetchJson  network utils

const composerStrategy = {
    // The file name that this strategy handles.  strategy 
    fileName: 'composer.json',

    /**
     * Parses the composer.json file content and extracts the dependencies.
     * @param {string} fileContent The content of the composer.json file.
     * @returns {object} An object containing the dependencies.
     */
    // Parses the composer.json file content and extracts the dependencies.
    // Parse  composer.json  dependencies
    parseDependencies(fileContent) {
        // Parse the JSON content of the file. Parse  JSON 
        const composerJson = JSON.parse(fileContent);
        // Return an object containing the require and require-dev dependencies.  object  dependencies  require  require-dev
        return { ...(composerJson.require || {}), ...(composerJson['require-dev'] || {}) };
    },

    /**
     * Fetches the license information for a given package name from Packagist.
     * @param {string} packageName The name of the package.
     * @returns {Promise<object>} An object containing the license and homepage information.
     */
    // Fetches the license information for a given package name from Packagist.
    //  license  package name  Packagist
    async fetchLicenseInfo(packageName) {
        // Fetch the package data from Packagist.  package  Packagist
        const response = await fetchJson(`https://repo.packagist.org/p2/${packageName}.json`);
        // Extract the package data from the response.  package  response
        const packageData = response.packages[packageName][0];
        // Return an object containing the license and homepage information.  object  license  homepage
        return {
            // Get the license from the package data, or 'N/A' if it is not available.  license  package  'N/A' 
            license: (packageData.license && packageData.license[0]) || 'N/A',
            // Get the homepage from the package data, or a default Packagist URL if it is not available.  homepage  package  URL  Packagist 
            homepage: packageData.homepage || `https://packagist.org/packages/${packageName}`
        };
    }
};

// Export the composerStrategy object.  object composerStrategy
module.exports = composerStrategy;