// This file defines the strategy for parsing Java Maven's pom.xml files and fetching license information.
//  strategy  parsing  pom.xml  Java Maven  license

const { fetchText } = require('../utils/network'); // Import the fetchText function from the network utils.  fetchText  network utils
const xml2js = require('xml2js'); // Import the xml2js library for parsing XML files.  xml2js  parsing  XML

const javaMavenStrategy = {
    // The file name that this strategy handles.  strategy 
    fileName: 'pom.xml',

    /**
     * Parses the pom.xml file content and extracts the dependencies.
     * @param {string} fileContent The content of the pom.xml file.
     * @returns {object} An object containing the dependencies.
     */
    // Parses the pom.xml file content and extracts the dependencies.
    // Parse  pom.xml  dependencies
    async parseDependencies(fileContent) {
        // Create a new XML parser.  XML parser 
        const parser = new xml2js.Parser();
        // Parse the XML file content into a JavaScript object. Parse  XML  JavaScript object
        const pom = await parser.parseStringPromise(fileContent);
        // Extract the dependencies from the parsed pom.  dependencies  pom  parse 
        const dependencies = pom.project?.dependencies?.[0]?.dependency || [];
        
        // Initialize an empty object to store the dependencies.  object  dependencies
        const deps = {};
        // Iterate over each dependency.  dependency
        dependencies.forEach(dep => {
            // Check if the dependency has a groupId, artifactId, and version.  dependency  groupId, artifactId  version 
            if (dep.groupId && dep.artifactId && dep.version) {
                 // Extract the groupId, artifactId, and version from the dependency.  groupId, artifactId  version  dependency
                 const groupId = dep.groupId[0];
                 const artifactId = dep.artifactId[0];
                 const version = dep.version[0];
                 // Add the dependency to the deps object using the format 'groupId:artifactId' as the key.  dependency  object deps  'groupId:artifactId'  key
                 deps[`${groupId}:${artifactId}`] = version;
            }
        });
        // Return the dependencies object.  object dependencies
        return deps;
    },

    /**
     * Fetches the license information for a given package name and version from Maven Central.
     * @param {string} packageName The name of the package (groupId:artifactId).
     * @param {string} packageVersion The version of the package.
     * @returns {Promise<object>} An object containing the license and homepage information.
     */
    // Fetches the license information for a given package name and version from Maven Central.
    //  license  package name  version  Maven Central
    async fetchLicenseInfo(packageName, packageVersion) {
        // Split the package name into groupId and artifactId.  package name  groupId  artifactId
        const [groupId, artifactId] = packageName.split(':');
        // Clean up version string from potential Maven property placeholders.  version string  Maven property placeholders 
        const version = packageVersion.replace(/\$|\{|\}/g, ''); 

        // Construct the URL to the .pom file directly from Maven Central.  URL  .pom  Maven Central
        const groupPath = groupId.replace(/\./g, '/');
        const pomUrl = `https://repo1.maven.org/maven2/${groupPath}/${artifactId}/${version}/${artifactId}-${version}.pom`;
        
        try {
            // Fetch the .pom file content.  .pom
            const pomContent = await fetchText(pomUrl);

            // Parse the fetched .pom XML content. Parse  XML  .pom 
            const parser = new xml2js.Parser({ explicitArray: false, ignoreAttrs: true });
            const pomData = await parser.parseStringPromise(pomContent);

            // Extract license information.  license
            let license = 'N/A';
            // Check if the pomData contains license information.  pomData  license 
            if (pomData.project && pomData.project.licenses && pomData.project.licenses.license) {
                // Handle both single and multiple licenses.  single  multiple licenses
                const licenses = Array.isArray(pomData.project.licenses.license) 
                    ? pomData.project.licenses.license 
                    : [pomData.project.licenses.license];
                
                // Extract the license names and join them with ' OR '.  license  ' OR '
                license = licenses.map(l => l.name || 'N/A').join(' OR ');
            }

            // Return an object containing the license and homepage information.  object  license  homepage
            return {
                // Set the license, or 'N/A' if no license is found.  license  'N/A'  license
                license: license || 'N/A',
                // Set the homepage, or a default Maven Repository URL if no homepage is found.  homepage  URL  Maven Repository  homepage
                homepage: (pomData.project && pomData.project.url) || `https://mvnrepository.com/artifact/${groupId}/${artifactId}/${version}`
            };

        } catch (error) {
            // Log the error message if fetching or parsing the .pom file fails.  error  parsing  .pom 
            console.error(`Failed to fetch/parse .pom for ${packageName}@${version}. URL: ${pomUrl}. Error: ${error.message}`);
            // Re-throw the error so the scanner can handle it and display a specific message. Re-throw error  scanner 
            throw error;
        }
    }
};

// Export the javaMavenStrategy object. 
//  object javaMavenStrategy
module.exports = javaMavenStrategy;