/**
 * Parses TOML files using a robust regex that handles bare and quoted keys.
 */
// Parses TOML files using a robust regex that handles bare and quoted keys.
// Parse  TOML  regex  bare  quoted keys
function parseTomlDecorations(document, text, depsMap, compliant, nonCompliant, unknown) {
    // Split the text into lines.  text  lines
    const lines = text.split('\n');
    // Define the dependency sections.  dependency sections
    const depSections = ["[dependencies]", "[dev-dependencies]", "[build-dependencies]", "[target", "[workspace.dependencies]"];
    // Flag to indicate if we are in a dependency section.  dependency section 
    let inDepSection = false;

    // This regex robustly captures TOML keys. It handles:
    // This regex robustly captures TOML keys. It handles:
    // Regex  TOML keys  :
    // 1. Quoted keys: "my-key" -> captures `my-key` in group 1
    // 1. Quoted keys: "my-key" -> captures `my-key` in group 1
    // 2. Bare keys: my_key  -> captures `my_key` in group 2
    // 2. Bare keys: my_key  -> captures `my_key` in group 2
    const keyRegex = /^\s*(?:"([^"]+)"|([a-zA-Z0-9_-]+))/;

    // Iterate over each line.  line
    for (let i = 0; i < lines.length; i++) {
        // Trim the line. Trim line
        const trimmedLine = lines[i].trim();
        
        // Ignore full-line comments.  full-line comments
        if (trimmedLine.startsWith("#")) continue;

        // If the line starts with a dependency section, set the inDepSection flag to true.  line  dependency section,  inDepSection flag  true
        if (depSections.some(s => trimmedLine.startsWith(s))) {
            // Set inDepSection to true.  inDepSection  true
            inDepSection = true;
            // Continue to the next line.  line 
            continue;
        }

        // If the line starts with a bracket and is not a dependency section, set the inDepSection flag to false.  line  bracket  dependency section,  inDepSection flag  false
        if (trimmedLine.startsWith("[") && !depSections.some(s => trimmedLine.startsWith(s))) {
            // Set inDepSection to false.  inDepSection  false
            inDepSection = false;
            // Continue to the next line.  line 
            continue;
        }

        // If we are in a dependency section and the line includes an equals sign, try to match the key regex.  dependency section  line ,  key regex
        if (inDepSection && trimmedLine.includes("=")) {
            // Match the key regex.  key regex
            const match = trimmedLine.match(keyRegex);
            
            // If there is a match, get the dependency name and data. ,  dependency name  data
            if (match) {
                // The dependency name will be in either the first or second capture group
                // The dependency name will be in either the first or second capture group
                // Dependency name  capture group 
                const depName = match[1] || match[2];
                // If there is a dependency name, get the dependency data.  dependency name,  dependency data
                if (depName) {
                    // Get the dependency data.  dependency data
                    const depData = depsMap.get(depName);
                    // If there is dependency data, create a decoration and add it to the appropriate array.  dependency data,  decoration  array 
                    if (depData) {
                        // Get the line.  line
                        const line = document.lineAt(i);
                        // Create the decoration.  decoration
                        const decoration = createDecoration(line, depData);
                        // Add the decoration.  decoration
                        addDecoration(decoration, depData, compliant, nonCompliant, unknown);
                    }
                }
            }
        }
    }
}
