/**
 * Parses files using regex and adds decorations for each dependency.
 * @param {vscode.TextDocument} document The document to parse.
 * @param {string} text The text of the document.
 * @param {Array<object>} relevantDeps The relevant dependencies.
 * @param {Array<object>} compliant The array of compliant decorations.
 * @param {Array<object>} nonCompliant The array of non-compliant decorations.
 * @param {Array<object>} unknown The array of unknown decorations.
 */
// Parses files using regex and adds decorations for each dependency.
// Parse  regex  decorations  dependency
function parseRegexDecorations(document, text, relevantDeps, compliant, nonCompliant, unknown) {
    // Iterate over each relevant dependency.  relevant dependency
     relevantDeps.forEach((dep) => {
        // Create a regex for the dependency.  regex  dependency
        const depRegex = new RegExp(`^\\s*${dep.name.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')}\\s`, "gm");
        // Match the regex against the text.  regex  text
        let match;
        // While there are matches, create a decoration and add it to the appropriate array.  matches,  decoration  array 
        while ((match = depRegex.exec(text)) !== null) {
            // Get the position of the match.  match
            const position = document.positionAt(match.index);
            // Get the line.  line
            const line = document.lineAt(position.line);
            // Create the decoration.  decoration
            const decoration = createDecoration(line, dep);
            // Add the decoration.  decoration
            addDecoration(decoration, dep, compliant, nonCompliant, unknown);
        }
    });
}

/**
 * Provides hover information for dependencies.
 */
// Provides hover information for dependencies.
//  hover  dependencies
class DependencyHoverProvider {
    /**
     * Constructor for the DependencyHoverProvider class.
     */
    // Constructor  DependencyHoverProvider
    constructor() {
      // Initialize the dependency data.  dependency data
      this.dependencyData = [];
    }
    /**
     * Updates the dependency data.
     * @param {Array<object>} data The dependency data.
     */
    // Updates the dependency data.
    //  dependency data
    updateData(data) {
      // Set the dependency data.  dependency data
      this.dependencyData = data || [];
    }
  
    /**
     * Provides hover information for the given document and position.
     * @param {vscode.TextDocument} document The document to provide hover information for.
     * @param {vscode.Position} position The position to provide hover information for.
     * @returns {vscode.Hover | null} The hover information, or null if no hover information is available.
     */
    // Provides hover information for the given document and position.
    //  hover  document  position 
    provideHover(document, position) {
      // Get the relative path of the document.  relative path  document
      const relativePath = vscode.workspace.asRelativePath(document.uri, false);
      // Filter the dependencies to only include those that are in the current document.  dependencies  dependencies  document 
      const relevantDeps = this.dependencyData.filter(d => d.manifestFile === relativePath);
      // If there are no relevant dependencies, return null.  relevant dependencies,  return null
      if(relevantDeps.length === 0) return null;
  
      // Get the word range at the position.  word range  position
      const range = document.getWordRangeAtPosition(position, /"([^"]+)"/) || document.getWordRangeAtPosition(position, /[a-zA-Z0-9_-]+/);
      // If there is no word range, return null.  word range,  return null
      if (!range) return null;
  
      // Get the hovered word.  hovered word
      const hoveredWord = document.getText(range).replace(/"/g, "");
      // Find the dependency data for the hovered word.  dependency data  hovered word
      const depData = relevantDeps.find((d) => d.name === hoveredWord);
  
      // If there is dependency data, create the hover content and return a new hover object.  dependency data,  hover content  return hover object 
      if (depData) {
        // Create the hover content.  hover content
        const content = new vscode.MarkdownString();
        // Set the content to be trusted.  content  trusted
        content.isTrusted = true;
        // Append the dependency name to the content.  dependency name  content
        content.appendMarkdown(`**LicenseSentinel: ${depData.name}**\n\n`);
        // Append the dependency version to the content.  dependency version  content
        content.appendMarkdown(`- **Version:** \`${depData.version}\`\n`);
        // Append the dependency license to the content.  dependency license  content
        content.appendMarkdown(`- **License:** \`${depData.license}\`\n`);
        // Define icons for each status.  icons 
        const icons = { compliant: "", "non-compliant": "", unknown: "" };
        // Append the dependency status to the content.  dependency status  content
        content.appendMarkdown(
          `- **Status:** ${icons[depData.status] || ''} \`${depData.status}\`\n\n`
        );
        // If there is a homepage, append a link to the homepage to the content.  homepage,  link  homepage  content
        if (depData.homepage) {
          // Append the link to the homepage to the content.  link  homepage  content
          content.appendMarkdown(`[Go to Homepage](${depData.homepage})`);
        }
        // Return a new hover object.  hover object 
        return new vscode.Hover(content, range);
      }
      // If there is no dependency data, return null.  dependency data,  return null
      return null;
    }
}
  
// Export the updateDecorations and DependencyHoverProvider functions.  updateDecorations  DependencyHoverProvider
module.exports = { updateDecorations, DependencyHoverProvider };