// This file defines the strategy for parsing Rust Cargo.toml files and fetching license information.
//  strategy  parsing  Rust Cargo.toml  license

const { fetchJson } = require('../utils/network'); // Import the fetchJson function from the network utils.  fetchJson  network utils
const toml = require('toml'); // Import the toml library for parsing TOML files.  toml  parsing  TOML

/**
 * Strategy for scanning Rust Dependencies (Cargo).
 * Strategy  Dependencies  Rust (Cargo)
 */
const rustCargoStrategy = {
    // The file name that this strategy handles.  strategy 
    fileName: 'Cargo.toml',

    /**
     * Reads the Cargo.toml file and extracts the list of dependencies.
     * @param {string} fileContent The content of the Cargo.toml file.
     * @returns {Object} An object of dependencies.
     */
    //  Cargo.toml  dependencies 
    // @param {string} fileContent  Cargo.toml
    // @returns {Object}  dependencies
    parseDependencies(fileContent) {
        // Parse the TOML content of the file. Parse  TOML 
        const parsedToml = toml.parse(fileContent);
        // Extract dependencies, dev-dependencies, and build-dependencies from the parsed TOML.  dependencies, dev-dependencies,  build-dependencies  TOML  parse 
        const dependencies = parsedToml.dependencies || {};
        const devDependencies = parsedToml['dev-dependencies'] || {};
        const buildDependencies = parsedToml['build-dependencies'] || {};
        
        // Combine all dependencies into one object.  dependencies  object 
        const allDeps = { ...dependencies, ...devDependencies, ...buildDependencies };
        // Initialize an empty object to store cleaned dependencies.  object  dependencies  clean 
        const cleanedDeps = {};

        // Cargo.toml can have dependencies in complex object formats.
        // Cargo.toml  dependency  object 
        // We simplify it by only taking the name and version.
        // 
        for (const [name, value] of Object.entries(allDeps)) {
            // If the value is a string, it's a simple version declaration.  value  string,  version declaration 
            if (typeof value === 'string') {
                // Add the dependency with its version to the cleaned dependencies.  dependency  version  cleaned dependencies
                cleanedDeps[name] = value;
            // If the value is an object and has a version property, use that version.  value  object  property version,  version 
            } else if (typeof value === 'object' && value.version) {
                // Add the dependency with its version to the cleaned dependencies.  dependency  version  cleaned dependencies
                cleanedDeps[name] = value.version;
            // If the value is an object and does not have a version property, treat as latest.  value  object  property version,  latest
            } else if (typeof value === 'object' && !value.version) {
                 // Add the dependency with "*" as version to the cleaned dependencies.  dependency  "*"  version  cleaned dependencies
                 cleanedDeps[name] = "*"; // No version specified, treat as latest
            }
        }
        
        // Return the cleaned dependencies object.  object cleaned dependencies
        return cleanedDeps;
    },

    /**
     * Fetches License information from crates.io API.
     * @param {string} packageName The name of the crate.
     * @returns {Promise<{license: string, homepage: string}>}
     */
    //  License  crates.io API
    // @param {string} packageName  crate
    // @returns {Promise<{license: string, homepage: string}>}
    async fetchLicenseInfo(packageName) {
        // crates.io API
        // Fetch the main crate data, which includes information on all versions.
        //  crate ,  versions
        const responseData = await fetchJson(`https://crates.io/api/v1/crates/${packageName}`);
        
        // --- START: Modified Section ---
        // --- START:  ---
        // The most recent version's data is in the first element of the 'versions' array.
        //  version  element  array 'versions'
        // This is more reliable than the top-level 'crate.license' which can be null.
        //  'crate.license'  null
        const latestVersionData = responseData.versions && responseData.versions[0];
        // Get the crate data.  crate
        const crateData = responseData.crate;

        // Get license from the latest version, which is the most accurate source.
        //  license  version , 
        const license = (latestVersionData && latestVersionData.license) || 'N/A';

        // Homepage and repository are usually on the main crate object.
        // Homepage  repository  object crate 
        const homepage = crateData.homepage || crateData.repository || `https://crates.io/crates/${packageName}`;
        
        // Return an object containing the license and homepage information.  object  license  homepage
        return {
            // Set the license.  license
            license: license,
            // Set the homepage.  homepage
            homepage: homepage
        };
        // --- END: Modified Section ---
        // --- END:  ---
    }
};

// Export the rustCargoStrategy object.  object rustCargoStrategy
module.exports = rustCargoStrategy;