// This file defines the strategy for parsing npm's package.json files and fetching license information.
//  strategy  parsing  package.json  npm  license

const { fetchJson } = require('../utils/network'); // Import the fetchJson function from the network utils.  fetchJson  network utils

const npmStrategy = {
    // The file name that this strategy handles.  strategy 
    fileName: 'package.json',
    
    /**
     * Parses the package.json file content and extracts the dependencies.
     * @param {string} fileContent The content of the package.json file.
     * @returns {object} An object containing the dependencies.
     */
    // Parses the package.json file content and extracts the dependencies.
    // Parse  package.json  dependencies
    parseDependencies(fileContent) {
        // Parse the JSON content of the file. Parse  JSON 
        const packageJson = JSON.parse(fileContent);
        // Return an object containing the require and require-dev dependencies.  object  dependencies  dependencies  devDependencies
        return { ...(packageJson.dependencies || {}), ...(packageJson.devDependencies || {}) };
    },

    /**
     * Fetches the license information for a given package name from the npm registry.
     * @param {string} packageName The name of the package.
     * @returns {Promise<object>} An object containing the license and homepage information.
     */
    // Fetches the license information for a given package name from the npm registry.
    //  license  package name  npm registry
    async fetchLicenseInfo(packageName) {
        // URL-encode the package name to handle scoped packages (e.g., @scope/package).
        // URL-encode package name  scoped packages ( @scope/package)
        // The '/' character must be encoded as '%2f' for the npm registry API.
        //  '/'  encode  '%2f'  npm registry API
        const encodedPackageName = packageName.replace('/', '%2f');
        
        // Fetch the package data from the npm registry.  package  npm registry
        const responseData = await fetchJson(`https://registry.npmjs.org/${encodedPackageName}/latest`);

        // Return an object containing the license and homepage information.  object  license  homepage
        return {
            // Get the license from the response data, or 'N/A' if it is not available.  license  response  'N/A' 
            license: responseData.license || 'N/A',
            // Get the homepage from the response data, or a default npm URL if it is not available.  homepage  response  URL  npm 
            homepage: responseData.homepage || `https://www.npmjs.com/package/${packageName}`
        };
    }
};

// Export the npmStrategy object.  object npmStrategy
module.exports = npmStrategy;