// This file defines the strategy for parsing Python Poetry's pyproject.toml files and fetching license information.
//  strategy  parsing  pyproject.toml  Python Poetry  license

const { fetchJson } = require('../utils/network'); // Import the fetchJson function from the network utils.  fetchJson  network utils
const toml = require('toml'); // Import the toml library for parsing TOML files.  toml  parsing  TOML

const pythonPoetryStrategy = {
    // The file name that this strategy handles.  strategy 
    fileName: 'pyproject.toml',

    /**
     * Parses the pyproject.toml file content and extracts the dependencies.
     * @param {string} fileContent The content of the pyproject.toml file.
     * @returns {object} An object containing the dependencies.
     */
    // Parses the pyproject.toml file content and extracts the dependencies.
    // Parse  pyproject.toml  dependencies
    parseDependencies(fileContent) {
        // Parse the TOML content of the file. Parse  TOML 
        const parsedToml = toml.parse(fileContent);
        // Extract dependencies and dev-dependencies from the parsed TOML.  dependencies  dev-dependencies  TOML  parse 
        const dependencies = parsedToml.tool?.poetry?.dependencies || {};
        const devDependencies = parsedToml.tool?.poetry?.['dev-dependencies'] || {};
        // Remove the python version dependency.  dependency  python version 
        delete dependencies.python;
        delete devDependencies.python;
        // Return an object containing the dependencies and dev-dependencies.  object  dependencies  dev-dependencies
        return { ...dependencies, ...devDependencies };
    },

    /**
     * Fetches the license information for a given package name from the PyPI API.
     * @param {string} packageName The name of the package.
     * @returns {Promise<object>} An object containing the license and homepage information.
     */
    // Fetches the license information for a given package name from the PyPI API.
    //  license  package name  PyPI API
    async fetchLicenseInfo(packageName) {
        // Fetch the package data from the PyPI API.  package  PyPI API
        const responseData = await fetchJson(`https://pypi.org/pypi/${packageName}/json`);
        // Extract the info from the response data.  info  response data
        const info = responseData.info;
        // Get the license from the info, or 'N/A' if it is not available.  license  info  'N/A' 
        const license = info.license || 'N/A';
        
        // PyPI does not have a reliable license field, so we need to look at the classifiers.
        // PyPI  license field   classifiers
        const classifiers = info.classifiers || [];
        // Find the license classifier.  license classifier
        const licenseClassifier = classifiers.find(c => c.startsWith('License ::'));
        
        // Initialize the clean license with the original license value.  clean license  license 
        let cleanLicense = license;
        // If a license classifier is found, extract the license name from it.  license classifier,  license  classifier
        if (licenseClassifier) {
            cleanLicense = licenseClassifier.split('::').pop().trim();
        }

        // Avoid generic, unhelpful license strings.  license strings  generic 
        if (cleanLicense === 'OSI Approved' || !cleanLicense || cleanLicense.toLowerCase() === 'n/a') {
            cleanLicense = license || 'N/A';
        }

        // Return an object containing the license and homepage information.  object  license  homepage
        return {
            // Set the license.  license
            license: cleanLicense,
            // Set the homepage.  homepage
            homepage: info.project_url || info.home_page || `https://pypi.org/project/${packageName}`
        };
    }
};

// Export the pythonPoetryStrategy object.  object pythonPoetryStrategy
module.exports = pythonPoetryStrategy;